// mixins

@mixin limitMaxRows($rows: 4) {
  display: -webkit-box;
  -webkit-line-clamp: $rows;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

// common
.container {
  max-width: 1130px;
  padding: 0 10px;
  margin: 0 auto;
}
.App {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}
// </>common

// =====HEADER=====
.header {
  box-shadow: 2px 2px 0 2px #f8f8f8;
  // fix scroll for cart counter
  overflow: hidden;
}
.header-contacts-outer {
  background: #121212;
  color: rgba(255, 255, 255, 0.8);
  font-size: 11px;
  padding: 6px 0;
}
.header-contacts-inner {
  display: flex;
  align-items: flex-end;
  svg:not(.social-icons__item) {
    margin-right: 10px;
  }
  &__item:not(:last-child) {
    margin-right: 30px;
  }

  //&__item:nth-child(2){
  //  font-family: 'Inter', sans-serif;
  //  letter-spacing: 0.03em;
  //}
  &__item:last-child {
    margin-left: auto;
  }
}
.social-icons__item {
  margin-left: 12px;
}
.header-menu-wrapper {
  display: flex;
  padding: 15px 0;
  justify-content: space-between;
  align-items: center;
  @media screen and (max-width: 560px) {
    flex-direction: column;
    gap: 20px;
  }
}
.header-logo {
  font-family: "Playfair Display SC", serif;
  font-size: 24px;
}
.header-nav {
  font-size: 14px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  max-width: 585px;
  width: 100%;
  margin: 0 10px;
  &__item--active {
    font-weight: bold;
  }
}
.header-controls {
  display: flex;
  justify-content: space-between;
  max-width: 164px;
  width: 100%;
  svg {
    width: 18px;
    height: 18px;
  }
  &__item {
    position: relative;
    display: flex;
  }
  .header-cart-counter {
    position: absolute;
    top: -7px;
    right: -11px;
    font-size: 9px;
    color: white;
    background: #121212;
    padding: 5px 5px 2px;
    border-radius: 50%;
    line-height: 1;
    font-weight: bold;
    border: 2px solid white;
  }
}
// =====</>HEADER=====

// =====MAIN-PROMO=====

.main-promo-outer {
  padding: 32px 0;
}

.main-promo-inner {
  min-height: 490px;
  display: flex;
  flex-wrap: wrap;
  gap: 30px;
  justify-content: center;
  &--left,
  &--right {
    max-width: 540px;
    width: 100%;
    display: flex;
  }
  .main-promo-inner--left {
    background: url("./components/promo/images/banner.jpg") center;
    justify-content: space-between;
    align-items: center;
    text-transform: uppercase;
  }
  .main-promo-inner--right {
    flex-wrap: wrap;
    // â†“ helps to set manual height with gap, but overflows
    //align-content: flex-start;
    gap: 30px;
    justify-content: center;
  }
  &__category {
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    p {
      text-transform: uppercase;
      font-size: 14px;
      font-weight: 600;
      letter-spacing: 0.06em;
      background: white;
      padding: 13px 24px;
      line-height: 1;
    }
    @media screen and (max-width: 576px) {
      flex-grow: 1;
    }
  }
  &__category:nth-child(1) {
    flex-basis: 255px;
    background: url("./components/promo/images/women.jpg") center no-repeat;
    background-size: cover;
  }
  &__category:nth-child(2) {
    flex-basis: 255px;
    background: url("./components/promo/images/men.jpg") center no-repeat;
    background-size: cover;
  }
  &__category:nth-child(3) {
    flex-basis: 255px;

    flex-grow: 1;
    background: url("./components/promo/images/accessor.jpg") center no-repeat;
    background-size: cover;
  }
}

//promo-slider
.slider-banner {
  background: white;
  padding: 24px 32px;
  text-align: center;
  &__header {
    font-size: 12px;
    font-weight: 600;
    letter-spacing: 0.06em;
    margin-bottom: 14px;
    color: rgba(18, 18, 18, 0.4);
  }
  &__text {
    font-size: 22px;
    font-weight: 600;
    letter-spacing: 0.06em;
  }
}
.slider-button {
  background: white;
  //padding: 16px;
  width: 40px;
  height: 40px;
  border: none;
  border-radius: 50%;
  position: relative;
  opacity: 0.6;
  cursor: pointer;
  &--left {
    margin-left: 24px;
  }
  &--right {
    margin-right: 24px;
    transform: rotate(180deg);
  }
  &--top {
    transform: rotate(90deg);
    margin-right: 14px;
  }
  &--bottom {
    transform: rotate(-90deg);
  }
  svg {
    width: 50%;
    margin: auto;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}
.slider-nav-img {
  max-width: 94px;
  max-height: 114px;
  position: relative;
  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}
// =====</>MAIN-PROMO=====

// =====PROMO-DELIVERY=====

.promo-delivery-outer {
  padding-bottom: 30px;
  box-shadow: 2px 2px 0 2px #f8f8f8;
}
.promo-delivery-inner {
  display: flex;
  justify-content: space-between;
  @media screen and (max-width: 400px) {
    flex-wrap: wrap;
