      //line-height: 22px;
    }
  }
}

// =====</>PROMO-DELIVERY=====

// =====PROMO-PRODUCTS=====
.promo-products-outer {
  margin-top: 96px;
}
.promo-products-inner {
  display: flex;
  align-items: center;
  margin-bottom: 48px;
  &-header {
    font-size: 22px;
    font-weight: 600;
    letter-spacing: 0.06em;
  }
