  font-weight: 500;
  font-size: 13px;
  letter-spacing: 0.06em;
  background: #f8f8f8;
  border: none;
  cursor: pointer;
}
// =====</>PROMO-PRODUCTS=====

// =====PRODUCTS=====
.products-inner {
  display: flex;
  flex-wrap: wrap;
  gap: 30px;
  justify-content: center;
  &-card {
    font-size: 14px;
    position: relative;
  }
  &-card__discount {
    position: absolute;
