  }
}

.products-controls-wrapper {
  margin: 38px 0;
  display: flex;
  align-items: center;
}
.products-controls-button {
  display: flex;
  align-items: center;
  font-size: 13px;
  letter-spacing: 0.06em;
  cursor: pointer;

