    font-size: 12px;
    letter-spacing: 0.06em;
    text-transform: uppercase;
    margin-bottom: 32px;
  }
  &__text {
    font-size: 14px;
    font-weight: 500;
    &:not(:nth-child(1)) {
      margin-top: 12px;
