// =====PROMO-SUBSCRIBE=====

.promo-subscribe-outer {
  background: linear-gradient(180deg, #f3f2f2 0%, #dcdbdb 100%);
  padding: 60px 0;
  overflow: hidden;
}
.promo-subscribe-inner {
  display: flex;
  justify-content: center;
  align-items: center;
}
.promo-subscribe-form {
  position: relative;
  background: white;
  padding: 80px 96px 96px;
  display: flex;
  flex-direction: column;
  text-align: center;
  letter-spacing: 0.06em;
